import React, {Component} from 'react'

export default class Separator extends Component{
    render() {
        return (
            <div>
                <div id="colorlib-counter" className="colorlib-counters" style={{backgroundImage: 'url(images/separatorbg1.jpg)'}} data-stellar-background-ratio="0.5">
        <div className="overlay" />
        <div className="colorlib-narrow-content">
          <div className="row">
          </div>
          <div className="row">
            <div className="col-md-3 text-center animate-box">
              <span className="colorlib-counter js-counter" data-from={0} data-to={87} data-speed={5000} data-refresh-interval={50} />
              <span className="colorlib-counter-label">Sensores</span>
            </div>
            <div className="col-md-3 text-center animate-box">
              <span className="colorlib-counter js-counter" data-from={0} data-to={14} data-speed={5000} data-refresh-interval={50} />
              <span className="colorlib-counter-label">Proyectos</span>
            </div>
            <div className="col-md-3 text-center animate-box">
              <span className="colorlib-counter js-counter" data-from={0} data-to={33} data-speed={5000} data-refresh-interval={50} />
              <span className="colorlib-counter-label">Clientes</span>
            </div>
            <div className="col-md-3 text-center animate-box">
              <span className="colorlib-counter js-counter" data-from={0} data-to={10} data-speed={5000} data-refresh-interval={50} />
              <span className="colorlib-counter-label">Partners</span>
            </div>
          </div>
        </div>
      </div>
            </div>
        )
    }
}